/* noto-sans-regular */
@font-face {
  font-family: "Noto Sans";
  font-style: normal;
  font-weight: 400;
  src: local(""),
    url("/assets/fonts/noto-sans-v11-regular.woff2") format("woff2"),
    url("/assets/fonts/noto-sans-v11-regular.woff") format("woff");
}
/* noto-sans-italic */
@font-face {
  font-family: "Noto Sans";
  font-style: italic;
  font-weight: 400;
  src: local(""),
    url("/assets/fonts/noto-sans-v11-italic.woff2") format("woff2"),
    url("/assets/fonts/noto-sans-v11-italic.woff") format("woff");
}
/* noto-sans-700 */
@font-face {
  font-family: "Noto Sans";
